import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store({
    strict:true,
    state:{
        gitToken:'',

        name:'',
        updated:'',
        description:'',
        ownerAvatarUrl:'',
        remarkText:'',

        gitData:[],
        gitData1:[],
    },
    mutations:{
        setToken(state,val){
            state.gitToken = val;
        },
        setName(state,val){
            state.name = val;
        },
        setDescription(state,val){
            state.description = val;
        },
        setUpdated(state,val){
            state.updated = val;
        },
        setOwnerAvatarUrl(state,val){
            state.ownerAvatarUrl = val;
        },
        setRemarkText(state,val){
            state.remarkText = val;
        },
        emptyGitData(state){
            state.gitData = 0;
        },
        reDataGit(state){
            state.gitData = state.gitData1.slice();
        },
        setGitData(state,val){
            state.gitData = val;
        }
    },
    actions:{
        getData1(store){
            axios({
                methods:'get',
                url:'https://api.github.com/user/repos',
                headers:{Authorization:'token '+store.state.gitToken}
            }).then((res)=>{
                
                // 修改store中state的数据的唯一方法是通过调用dispatch或者commit，即使是在action中也要
                // 调用commit来修改。不然会报错。
                // store.state.gitData1 = res.data;

                store.commit('setGitData',res.data);

                // store.state.gitData = store.state.gitData1.slice();
            });
        },
       
    }
})

export default store


