import Vue from 'vue'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import App from './App'
import router from '@/routes/index.js'

import show from '@/css/px-to-rem.js'
import store from '@/store/store.js'
show();


Vue.use(ElementUI);


new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
  store
})
import '@/css/index.css'


