<template>
    <div id="home">
        <div class="header home_header" id="home_head">
            <h3 class="header_text header_left">首页</h3>
            <input type="button" class="header_button header_right" value="退出" @click="logout">
        </div>
        <div id="home_info">
            <div id="user_profile">
                <img :src="ownerAvatarUrl" alt="">
            </div>
            <div id="user_info" class="home_info">
                <div >
                    <span>项目名：{{name}}</span>
                </div>
                <div>
                    <span>项目时间：{{updated}}</span>
                </div>
                <div>
                     <span>项目描述：{{description}}</span>
                </div>
                <div>
                    <span>项目备注：{{remarkText}}</span>
                </div>
              
            </div>
            <div id="home_footer">
                <input class="large_button" type="button" value="重设封面" @click="resetPost">
            </div>
        </div>
    </div>
</template>

<script>

export default {

    computed:{
        ownerAvatarUrl(){
            return this.$store.state.ownerAvatarUrl;
        },
        name(){
            return this.$store.state.name;
        },
        updated(){
            return this.$store.state.updated;
        },
        description(){
            return this.$store.state.description
        },
        remarkText(){
            return this.$store.state.remarkText
        }
    },
    methods:{
        resetPost(){
            this.$store.dispatch('getData1')
            this.$router.replace({name:'coverPages'})
            this.$store.commit('setName','')
            this.$store.commit('setDescription','')
            this.$store.commit('setUpdated','')
            this.$store.commit('setOwnerAvatarUrl','')
            this.$store.commit('setRemarkText','');
            this.$store.commit('setToken',localStorage.getItem('firstToken'))

        },
        logout(){
            localStorage.removeItem('firstToken')
            this.$router.replace('/');

        }
    }
}
</script>

<style>

</style>



