<template>
    <div id="login">
        <div id="login_head" class="header header_center">
            <h3 class="header_text">登录</h3>
        </div>
        <div id="login_profile">
            <img src="@/img/tx.png" class="img_profile" alt="">
        </div>
        <div id="login_info">
            <input type="text" name="token" ref="token" placeholder="token" class="large_text">
            <br />
            <input type="button" @click="login" value="登录" class="large_button">
        </div>
        <div class="footer_info"><span>DAMN_AMAZING</span></div>
    </div>
</template>

<script>
export default {
    methods:{
        login(){
            let gitToken = this.$refs.token.value
            localStorage.setItem('firstToken',gitToken)
            if(gitToken != '' && gitToken != 'token'){
                this.$store.commit('setToken',gitToken);

                //此处默认进入封面，如果封面没有设置，进入设置
                if(localStorage.getItem('setName')){
                    this.$router.replace('/home');

                    //调用本地store，将原设定的首页值设置进去
                    this.setHome();
                }else{
                    // 进入设置面
                    this.$router.push('/coverSetting');

                    // 将token存入vuex的store
                    this.$store.commit('setToken',gitToken);
                   
                }
            }else{
                this.$refs.token.style.borderColor = 'red';
            }
        },
        setHome(){
            this.$store.commit('setName',localStorage.getItem('setName'));
            this.$store.commit('setDescription',localStorage.getItem('setDescription'));
            this.$store.commit('setUpdated',localStorage.getItem('setUpdated'));
            this.$store.commit('setOwnerAvatarUrl',localStorage.getItem('setOwnerAvatarUrl'));
            this.$store.commit('setRemarkText',localStorage.getItem('setRemarkText'));
        }
    },
    mounted(){
        // 获得本地的token，如果没有,gitToken就是null,那么默认进入login登录
        // 如果 有token并且有设置过首页 那么就进入home首页
        let gitToken = localStorage.getItem('firstToken');
        if(gitToken && localStorage.getItem('setName')){
            this.$router.replace({name:'home'});
            this.setHome();
            this.$store.commit('setToken',gitToken);
        }
    }
}
</script>

<style>

</style>



