import Vue from 'vue'
import vueRouter from 'vue-router'
import login from '@/components/login'
import home from '@/components/home.vue'
import coverSetting from '@/components/coverSetting'
import coverSettingPages from '@/components/coverSettingPages'
import coverRemark from '@/components/coverRemark'

Vue.use(vueRouter)

let router = new vueRouter({
    routes:[
        {
            path:'/login',
            component:login,
            name:'login'
        },{
            path:'/home',
            component:home,
            name:'home'
        },{
            path:'/coverSetting',
            component:coverSetting,
            name:'coverSetting'
        },{
            path:'/coverPages',
            component:coverSettingPages,
            name:'coverPages',
            children:[
                {
                    path:'/coverPages/coverRemark',
                    component:coverRemark,
                    name:'coverRemark'
                }
            ]
        }
        ,{
            path:'/',
            redirect:'/login'
        }
    ]
})
export default router


