<template>
    <div class="cover_remark">
        <textarea placeholder="这是备注内容" cols="30" rows="10" @click="select($event)"></textarea>
    </div>
</template>

<script>
export default {
    methods:{
        select(ev){
            ev.target.style.borderColor = '#8CCF5E';
        }
    }
}
</script>

<style>

</style>



