<template>
    <div id="cover_setting_pages">
        <div id="cover_setting_pages_head" class="header">
            <h3 class="header_text header_left">{{settingItem[item]}}</h3>
            <input type="button" class="header_right" value="退出" @click="logout">
        </div>
        <div id="cover_setting_pages_info" ref="listFather">
            <div :class="{cover_list:true,cover_list_click:i===index}" v-for="(d,i) in gitData" :key="i" @click="select1($event,i)">
                <span v-if="item <=3">{{d[itemData[item]]}}</span>
                <img v-if='item == 3' :src="d.owner.avatar_url" alt="">
            </div>
        </div>
        <router-view ref="remark"></router-view>
        <div id="footer_button">
            <input class="stepUp" type="button" value="上一步" @click="goBack">
            <input class="stepDown" type="button" value="下一步" @click="nextStep" ref='ok' disabled="disabled">
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            settingItem:['选择项目名','选择项目描述','选择项目时间','选择项目头像','输入备注信息'],
            itemData:['name','description','updated_at','name'],
            item: 0,
            info:'',
            url:'',
            index:-1
        }
    },
    computed:{
        gitData(){
            return this.$store.state.gitData;
        }
    },
    methods:{
        logout(){
            // 退出，清除localStore并返回登录页
            localStorage.removeItem('firstToken');
            this.$router.replace({name:'login'});
            this.item = 0;
        },
        select1(ev,i){
            // 点击列表中的一行，其边框color变绿，实现方法为！！！通过加入一个class其样式已在css中写好，将该行的i传递给组件的index，
            // 如果两者全等时为true，即可成立对应的class。
            // value存在组件的info上
            this.index = i;

            //将下一步按钮改为可用
            this.$refs.ok.disabled = false;
            this.$refs.ok.style.background = '#8CCF5E';

            // for(var i = 0;i<ev.currentTarget.parentElement.children.length;i++){
            //     ev.currentTarget.parentElement.children[i].style.borderColor = 'lightgray';
            // };

            // ev.currentTarget.style.borderColor = 'green';

            //当选头像的时候，传入的是img标签的src
            if(this.item <= 2){
                this.info = ev.currentTarget.firstElementChild.innerText;
            }else if(this.item == 3){
                this.info = ev.currentTarget.firstElementChild.nextElementSibling.src;
            }
        },
        goBack(){
            //返回时将组件的index改为-1，使选项所有框没有变色
            this.index = -1;

            if(this.item == 0){
                // item=0时返回设定页
                this.$router.push({name:'coverSetting'})
            }else if(this.item == 4){
                //组件item=3时，将store的gitData清空了以使选框不出现，由此步返回要重新赋值
                this.$store.commit('reDataGit');

                this.$router.push('/coverPages');
                this.$refs.ok.value = '下一步';
                this.item--
            }else{
                // 返回上一级，即item减1
                this.item--;
            }
            
            this.$refs.ok.disabled = 'disabled';
            this.$refs.ok.style.background = 'lightgray';
        },
        nextStep(){
            //下一步时将组件的index改为-1以使选框没有变色
            this.index = -1;

            let fnName = '';

            switch (this.item){
                case 0:
                    fnName = 'setName';
                    break;
                case 1:
                    fnName = 'setDescription';
                    break;
                case 2:
                    fnName = 'setUpdated';
                    break;
                case 3:
                    fnName = 'setOwnerAvatarUrl';
                    this.$router.push({name:'coverRemark'});
                    this.$refs.ok.value = '完成';
                    this.$store.commit('emptyGitData');
                    break;
                case 4:
                    this.$router.push('/coverPages');
                    this.$router.push('/home');
                    this.$refs.ok.value = '下一步';
                    fnName = 'setRemarkText';
                    this.info = this.$refs.remark.$el.children[0].value;   
                    break;
            }

            //用以本地store的名字干脆跟vuex的函数名一样好了
            localStorage.setItem(fnName,this.info);

            this.$store.commit(fnName,this.info);
            this.item++;
            if(this.item == 4){
                this.$refs.ok.disabled = false;
                this.$refs.ok.style.background = '#8CCF5E';
            }else if(this.item == 5){
                this.item = 0;
            }else{
                this.$refs.ok.disabled = 'disabled';
                this.$refs.ok.style.background = 'lightgray'; 
            }
      
        },

        //深拷贝
        deepCopy(obj){
            var result = Array.isArray(obj) ? [] : {};
            for (var key in obj) {
                if (obj.hasOwnProperty(key)) {
                if (typeof obj[key] === 'object' && obj[key]!==null) {
                    result[key] = deepCopy(obj[key]);   //递归复制
                } else {
                    result[key] = obj[key];
                }
                }
            }
            return result;
        }
    }
}
</script>

<style>

</style>



