<template>
    <div id="cover_setting">
        <div class="header" id="cover_head">
            <h3 class="header_left  header_text">首页</h3>
            <input class="header_right" type="button" value="退出">
        </div>
        <div id="cover">
            <img src="@/img/tx.png" class="img_profile" alt="">
        </div>
        <div id="cover_content">
            <input class="large_button" type="button" value="设置封面" @click="coverGetData">
        </div>
        <div class="footer_info" id="footer">DAMN_AMAZING</div>
    </div>
</template>

<script>
export default {
    methods:{
        // 点击设置封面后进入后续页面
        coverGetData(){
            this.$store.dispatch('getData1');
            this.$router.push('/coverPages');
        }
    }
}
</script>

<style>

</style>



